<template>
  <form class="card auth-card" @submit.prevent="submitHandler">
    <div class="card-content">
      <span class="card-title">Домашняя бухгалтерия</span>
      <div class="input-field">
        <input
          id="email"
          type="text"
          v-model.trim="state.email"
          :class="{ invalid: v$.email.$errors.length }"
        />
        <label for="email">Email</label>
        <small v-if="v$.email.$errors.length" class="helper-text invalid">
          Введите корректный Email
        </small>
      </div>
      <div class="input-field">
        <input
          id="password"
          type="password"
          v-model="state.password"
          :class="{ invalid: v$.password.$errors.length }"
        />
        <label for="password">Пароль</label>
        <small
          v-for="error of v$.password.$errors"
          :key="error.$uid"
          class="helper-text invalid"
        >
          Пароль должен быть {{ v$.password.minLength.$params.min }} символов, а
          сейчас он {{ state.password.length }}
        </small>
      </div>
      <div class="input-field">
        <input
          id="name"
          type="text"
          v-model="state.name"
          :class="{ invalid: v$.name.$errors.length }"
        />
        <label for="name">Имя</label>
        <small class="helper-text invalid" v-if="v$.name.$errors.length">
          Поле имя не должно быть пустым
        </small>
      </div>
      <p>
        <label>
          <input type="checkbox" v-model="state.agree" />
          <span>С правилами согласен</span>
        </label>
      </p>
    </div>
    <div class="card-action">
      <div>
        <button class="btn waves-effect waves-light auth-submit" type="submit">
          Зарегистрироваться
          <i class="material-icons right">send</i>
        </button>
      </div>

      <p class="center">
        Уже есть аккаунт?
        <router-link to="/login">Войти!</router-link>
      </p>
    </div>
  </form>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, email, minLength } from "@vuelidate/validators";
import { reactive } from "@vue/reactivity";

export default {
  name: "register",
  setup() {
    const state = reactive({
      password: "",
      email: "",
      name: "",
      agree: false,
    });
    const rules = {
      email: { required, email },
      password: { required, minLength: minLength(6) },
      name: { required },
      agree: { checked: (v) => v },
    };

    const v$ = useVuelidate(rules, state);
    return { state, v$ };
  },

  data() {
    return {
      email: "",
      password: "",
      name: "",
      agree: false,
    };
  },
  methods: {
    async submitHandler() {
      if (this.v$.$invalid) {
        this.v$.$touch();
        return;
      }
      const formData = {
        email: this.state.email,
        password: this.state.password,
        name: this.state.name,
      };

      try {
        await this.$store.dispatch("register", formData);
        this.$router.push("/");
      } catch (e) {}
    },
  },
};
</script>
